import { configureStore, createSlice } from "@reduxjs/toolkit";

const cart = createSlice({
  name: 'cart',
  initialState: [], 
  reducers: {
    addItem(state, action) { //addItem    state        
      //state.push(action.payload) //   ->    ()      
      let index = state.findIndex((findId) => { 
        return findId.id === action.payload.id
      });
      if(index > -1) {
        state[index].count++
      } else {
          state.push(action.payload)
      }
    }, //addItem 
    deleteItem(state, action){
      let index = state.findIndex((findId) => { 
        return findId.id === action.payload})
      //            id  
      state.splice(index, 1)
    }, //deleteItem
    addCount(state, action){
      let index = state.findIndex((findId) => {
        return findId.id === action.payload})
        state[index].count++
    }, //addCount
    subCount(state, action){
      let index = state.findIndex((findId) => {
        return findId.id === action.payload})
      if(state[index].count > 1) state[index].count--
    } //subCount
  }
})

//setter    addItem    reducers action , dispatch 
export let { addItem, deleteItem, addCount, subCount } = cart.actions

//    (import)  state 
export default configureStore({
  reducer: {
    cart: cart.reducer //cart state   -> useSelector       useSelector 
  }
})